import { NgModule } from '@angular/core';
import { CommonModule, } from '@angular/common';
import { BrowserModule  } from '@angular/platform-browser';
import { Routes, RouterModule } from '@angular/router';

import { ComponentsComponent } from './components/components.component';
import { ProfileComponent } from './public/profile/profile.component';
import { SignupComponent } from './public/signup/signup.component';
import { LandingComponent } from './public/landing/landing.component';
import { AnotacionesComponent } from './public/anotaciones/anotaciones.component';
import { ShoppingCartComponent } from './shared/shopping-cart/shopping-cart.component';
import { DetalleRetornoComponent } from './shared/web-pay/detalle-retorno/detalle-retorno.component';

const routes: Routes =[
    { path: '', redirectTo: 'landing', pathMatch: 'full' },
    { path: 'home',             component: ComponentsComponent },
    { path: 'user-profile',     component: ProfileComponent },
    { path: 'signup',           component: SignupComponent },
    { path: 'landing',          component: LandingComponent },
    { path: 'anotaciones',      component: AnotacionesComponent },
    { path: 'carrocompra',      component: ShoppingCartComponent },
    { path: 'web-pay/detalle-retorno/:idPago',      component: DetalleRetornoComponent },
];

@NgModule({
  imports: [
    CommonModule,
    BrowserModule,
    RouterModule.forRoot(routes,{
      useHash: false,
      scrollPositionRestoration: 'enabled',
      anchorScrolling: 'enabled',
      scrollOffset: [0, 25], // cool option, or ideal option when you have a fixed header on top.
    })
  ],
  exports: [
  ],
})
export class AppRoutingModule { }
