import { Component, OnInit } from '@angular/core';
import { ShoppingCartModel } from '../@models/shopping-cart.model';
import { ShoppingCartService } from './shopping-cart.service';

// Services
import { WebPayService } from "app/shared/@services/web-pay.service";

// Model
import { environment } from 'environments/environment';
import { Router } from '@angular/router';

@Component({
  selector: 'app-shopping-cart',
  templateUrl: './shopping-cart.component.html',
  styleUrls: ['./shopping-cart.component.css']
})
export class ShoppingCartComponent implements OnInit {

  shoppingCart: ShoppingCartModel[] = null;
  total: number = 0;
  WebPayReturnUrl = environment.WebPayReturnUrl;
  WebPayUrl = environment.WebPayUrl;
  WebPayReturnShroppingCart = environment.WebPayReturnShroppingCart;

  constructor(
    private shoppingCartService: ShoppingCartService,
    private router: Router) { }

  ngOnInit(): void {
    this.shoppingCartService.shoppingCartObservable.subscribe(
      (shoppingCart: ShoppingCartModel[]) => {
        console.log("shoppingCart.length", shoppingCart.length);
        if( shoppingCart.length === 0 )
          this.router.navigate(['/landing']);
        this.shoppingCart = shoppingCart;
        this.total = 0;
        this.shoppingCart.forEach( value => {
          this.total += (value.cantidad * value.producto.precio);
        });
      }
    );
  }
  addProduct(shoppingCartModel: ShoppingCartModel) {
    this.shoppingCartService.addProduct(shoppingCartModel.producto);
  }
  deleteProduct(shoppingCartModel: ShoppingCartModel) {
    this.shoppingCartService.deleteProduct(shoppingCartModel.producto);
  }
  clearProduct() {
    this.shoppingCartService.clearProduct();
  }
  comprar(): void {

    const formWebPay = document.getElementById('formWebPay') as HTMLFormElement;
    formWebPay.submit();
    this.clearProduct();
  }
  toDataString() {
    return JSON.stringify( { shoppingCart : this.shoppingCart, total: this.total} );
  }
}
