import { Injectable } from "@angular/core";
import { BehaviorSubject } from "rxjs";
import { filter } from "rxjs/operators";

// Model
import { ShoppingCartModel } from "app/shared/@models/shopping-cart.model";
import { ProductoModel } from "app/public/@models/producto.model";

@Injectable({
  providedIn: 'root',
})
export class ShoppingCartService {
  private nameStorage: string = "shoppingCart";
  private _shoppingCarts : ShoppingCartModel[] = [];
  public _shoppingCart = new BehaviorSubject<ShoppingCartModel[]>(null);
  public readonly shoppingCartObservable = this._shoppingCart.asObservable().pipe(filter( shoppingCart => shoppingCart != null));

  constructor() {
    this.initLoadData();
  }

  get shoppingCarts() { return this._shoppingCarts; }

  initLoadData() {
    if( this._shoppingCarts.length === 0 ) {
      const data = this.getLocalStorage();
      if ( data ) {
        this._shoppingCarts = data;
        this._shoppingCart.next(this._shoppingCarts);
      } else {
        this._shoppingCarts = [];
        this._shoppingCart.next(this._shoppingCarts);
      }
    }
  }
  addProduct( producto: ProductoModel ) {
    let existe: boolean = false;
    this._shoppingCarts.forEach( value => {
      if( value.producto.id === producto.id ) {
        value.cantidad++;
        existe = true;
      }
    })

    if( !existe ) {
      const shoppingCart: ShoppingCartModel = {
        producto: producto,
        cantidad: 1
      };
      this._shoppingCarts.push(shoppingCart);
    }
    
    this._shoppingCart.next(this._shoppingCarts);
    this.addLocalStorage( this._shoppingCarts );
  }
  deleteProduct( producto: ProductoModel ) {
    this._shoppingCarts.forEach( (value, index) => {
      if( value.producto.id === producto.id ) {
        if( value.cantidad > 1 )
          value.cantidad--;
        else
          this._shoppingCarts.splice( index, 1 );
      }
    })
    
    this._shoppingCart.next(this._shoppingCarts);
    this.addLocalStorage( this._shoppingCarts );
  }
  clearProduct() {
    this._shoppingCarts = [];
    this._shoppingCart.next(this._shoppingCarts);
    this.deleteLocalStorage();
  }

  private addLocalStorage(data: ShoppingCartModel[]) {
    localStorage.setItem( this.nameStorage, JSON.stringify(data) )
  }
  private deleteLocalStorage() {
    localStorage.removeItem(this.nameStorage);
  }
  private getLocalStorage() {
    const data = localStorage.getItem(this.nameStorage);
    return  JSON.parse( data );
  }
}
