import { Component, OnInit } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { ActivatedRoute } from "@angular/router";
import { webPayModel } from 'app/shared/@models/web-pay.model';
import { CrudFirebase } from 'app/shared/firebase/crud-firebase';
import { environment } from 'environments/environment';

@Component({
  selector: 'app-detalle-retorno',
  templateUrl: './detalle-retorno.component.html',
  styleUrls: ['./detalle-retorno.component.css']
})
export class DetalleRetornoComponent implements OnInit {

  idPago: string = "";
  crudFirebaseShopper: CrudFirebase<webPayModel>;
  dataFront: webPayModel = null;
  alertType = {
    AUTHORIZED : {type: "success", descripcion: "Transaccin exitosa"},
    ABORTED : {type: "warning", descripcion: "Transaccin abortada por el usuario"},
    REJECTED : {type: "danger", descripcion: "Transaccin rechazada"},
    ERROR : {type: "danger", descripcion: "Error en la transaccin"},
  }
  tipoPago = {
    VD: "Venta Debito",
    VN: "Venta Normal",
    VC: "Venta en cuotas",
    SI: "3 cuotas sin interes",
    S2: "2 cuotas sin interes",
    NC: "N Cuotas sin intere",
    VP: "Venta Prepago",
  }

  constructor(
    private route: ActivatedRoute,
    private afs: AngularFirestore,) {
      this.crudFirebaseShopper = new CrudFirebase<webPayModel>(afs, `${environment.idProyecto}`);
    }

  ngOnInit(): void {
    this.idPago = this.route.snapshot.paramMap.get("idPago");
    this.getPago();
  }
  getPago() {
    console.log("ini")
    this.crudFirebaseShopper
      .getRef()
      .doc(environment.shopper)
      .collection(environment.anonimo)
      .doc(this.idPago)
      .onSnapshot((value) => value.exists ? (this.dataFront = value.data() as webPayModel) : null );
    console.log("fin")
  }

}