import { Component, OnInit } from "@angular/core";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { AngularFirestore } from '@angular/fire/firestore';
import { CrudFirebase } from "app/shared/firebase/crud-firebase";
import { environment } from "environments/environment";

// models
import { AdminModel } from "../@models/admin.model";
import { ProductoModel } from "../@models/producto.model";

// utils
import { Utils } from "app/shared/@utils/utils";
// Services
import { ShoppingCartService } from "app/shared/shopping-cart/shopping-cart.service";
// Modal
import { DetalleProductoModal } from "./modal/detalle-producto.modal";

@Component({
  selector: "app-landing",
  templateUrl: "./landing.component.html",
  styleUrls: ["./landing.component.scss"],
})
export class LandingComponent implements OnInit {
  crudFirebaseAdmin: CrudFirebase<AdminModel>;
  crudFirebaseProducto: CrudFirebase<ProductoModel>;
  focus: any;
  focus1: any;
  comunicar = {
    nombre: "",
    mensaje: "",
  };
  admin: AdminModel = null;
  productos: ProductoModel[] = [];

  constructor(
    private modalService: NgbModal,
    private afs: AngularFirestore,
    private shoppingCartService: ShoppingCartService, ) {
    this.crudFirebaseAdmin = new CrudFirebase<AdminModel>(afs, `${environment.idProyecto}`);
  }
  open(producto: ProductoModel) {
    const modalRef = this.modalService.open(DetalleProductoModal, { size: 'xl', scrollable: true });
    modalRef.componentInstance.producto = producto;
    modalRef.componentInstance.admin = this.admin;
  }

  ngOnInit(): void {
    this.crudFirebaseAdmin
      .getRef()
      .doc(environment.admin)
      .onSnapshot((admin) => admin.exists ? (this.admin = admin.data() as AdminModel) : null );
    
    this.crudFirebaseAdmin
      .getRef()
      .doc(environment.data)
      .collection( environment.productos )
      .onSnapshot( snapshot => {
        const productos = [];
        snapshot.forEach( data => {
          const producto = data.data() as ProductoModel;
          producto.id = data.id;
          productos.push( producto );
        });
        this.productos = productos;
      });
  }

  comunicarse() {
    Utils.sendMessageWhatsapp(this.admin.whatsapp, `Hola Soy ${this.comunicar.nombre}, ${this.comunicar.mensaje.replace( /(?:\r\n|\r|\n)/g, "%0A" )}`);
  }

  addProduct(producto: ProductoModel) {
    this.shoppingCartService.addProduct(producto);
  }
  deleteProduct(producto: ProductoModel) {
    this.shoppingCartService.deleteProduct(producto);
  }
}